html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

img {
  max-width: 100%;
  display: block;
}

body {
  font-family: var(--font-body);
  color: var(--black);
  line-height: 1.4;
  background-color: var(--white);
  margin: 0;
  padding: 0;
}

.breadcrumbs {
  margin-top: 1rem;
}

.breadcrumb {
  font-family: var(--font-header);
  font-size: 0.875rem;
  font-weight: 500;
  color: var(--lightGrey);
}
.breadcrumb__current {
  font-weight: 700;
}

.visually-hidden {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}
.visually-hidden:focus {
  left: 0;
  width: auto;
  height: auto;
  background-color: #fff;
  padding: 0.5rem;
  border: 1px solid;
}

hr {
  color: var(--darkGreen);
  background-color: var(--darkGreen);
  height: 0.125rem;
  margin-bottom: 3.75rem;
}

.wrapper {
  width: min(100% - 2.5rem, 75rem);
  margin-inline: auto;
}

.text__container {
  width: min(100% - 2.5rem, 46.25rem);
  margin-inline: auto;
}

.inner-container {
  width: 80%;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}
@media (max-width: 43.74875em) {
  .inner-container {
    margin: 0 auto;
    width: 90%;
  }
}

.btn {
  cursor: pointer;
  font-family: var(--font-header);
  font-size: 1.25rem;
  font-weight: 500;
  line-height: 1.375rem;
  padding: 0.5625rem 1.5rem;
  border-radius: 0.3125rem;
  text-transform: uppercase;
  letter-spacing: 1px;
}
.btn__contact a {
  background: linear-gradient(var(--yellow) 0 5%, var(--black));
  border: 1px solid var(--yellow);
  color: var(--white);
  margin-left: 1.25rem;
}
@media (max-width: 43.74875em) {
  .btn__contact a {
    margin-left: 0;
  }
}
.btn__contact a:hover {
  background: linear-gradient(var(--lightGreen) 0 5%, var(--black));
  border: 1px solid var(--lightGreen);
  transition: color 300ms ease-in-out;
}
.btn__moreInfo a {
  background: linear-gradient(var(--white) 0 30%, var(--black));
  border: 1px solid var(--white);
  color: var(--darkGreen);
}
.btn__moreInfo a:hover {
  background: linear-gradient(var(--yellow) 0 60%, var(--black));
  border: 1px solid var(--white);
  transition: color 200ms ease-in-out;
  margin-left: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: var(--font-header);
  line-height: 1.1;
  margin-top: 0;
  margin-bottom: 1.25rem;
}

h1 {
  font-size: 1.875rem;
  font-size: clamp(1.875rem, 0.98rem + 3.35vw, 3.5rem);
}

a,
a:visited,
a:active,
a:hover {
  text-decoration: none;
}

a[aria-current=true] {
  font-weight: 500;
  text-decoration: underline;
}

p {
  font-size: 1rem;
  font-weight: 500;
  margin-top: 0;
  line-height: 1.5rem;
}

:root {
  --darkGreen: hsl(167, 97%, 15%);
  --nwcGreen: hsl(166, 100%, 20%);
  --lightGreen: hsl(109, 61%, 43%);
  --northernBlue: hsl(208, 100%, 33.5%);
  --lightBlue: hsl(214, 58%, 55%);
  --orange: hsl(31, 100%, 50%);
  --red: hsl(347, 100%, 45.3%);
  --yellow: hsl(49, 100%, 50%);
  --darkBlue: hsl(197, 97%, 15%);
  --darkBlue75: rgba(1, 56, 77, 0.75);
  --white: hsl(240, 100%, 99%);
  --black: hsl(40, 8%, 15%);
  --black65: rgba(40, 38, 34, 0.65);
  --lightGrey: hsl(210, 1%, 61%) ;
}

:root {
  --font-header: "Raleway", sans-serif;
  --font-body: "Lato", sans-serif;
}

.card {
  padding: 1.25rem;
}

.hero__index {
  position: relative;
}
@media (max-width: 43.74875em) {
  .hero__index {
    width: 100%;
  }
}
.hero__indexContent {
  font-family: var(--font-header);
  color: var(--white);
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  bottom: 0;
  left: 0;
  background-color: var(--darkBlue75);
  z-index: 899;
}
@media (max-width: 43.74875em) {
  .hero__indexContent {
    width: 100%;
  }
}
.hero__content {
  font-family: var(--font-header);
  color: var(--white);
  display: flex;
  flex-direction: row;
  padding: 30px 25px 20px;
  gap: 16px;
}
@media (max-width: 43.74875em) {
  .hero__content {
    display: flex;
    flex-direction: column;
    padding: 0.75rem 0 0.9375rem;
    gap: 0;
  }
}
.hero__branding {
  width: 18.75rem;
}
@media (max-width: 43.74875em) {
  .hero__branding {
    width: 15.625rem;
  }
}
.hero__info {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
@media (max-width: 43.74875em) {
  .hero__info {
    text-align: center;
    margin: 0;
  }
}
.hero__date {
  font-size: 1.375rem;
  font-weight: 700;
}
@media (max-width: 43.74875em) {
  .hero__date {
    margin: 0;
  }
}
.hero__location {
  margin-bottom: 0;
}
.hero__hotel {
  font-size: 1.25rem;
  font-weight: 700;
}
.hero__place {
  font-size: 1.125rem;
  font-weight: 500;
}

.header {
  background-color: var(--darkGreen);
  display: flex;
  flex-direction: row;
  align-items: center;
  position: sticky;
  top: 0;
  width: 100%;
  box-shadow: 0 0.4375rem 0.5rem rgb(40, 38, 34);
  z-index: 999;
}
.header.open .overlay {
  display: block;
}
.header.open .header__toggle > span:first-child {
  transform: rotate(45deg);
}
.header.open .header__toggle > span:nth-child(2) {
  opacity: 0;
}
.header.open .header__toggle > span:last-child {
  transform: rotate(-45deg);
}
.header .overlay {
  opacity: 0;
  position: sticky;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: var(--darkGreen);
}
.header__logo {
  padding: 1rem 0;
}
.header__logo img {
  width: 8.75rem;
}
.header__toggle > span {
  display: block;
  width: 1.625rem;
  height: 0.125rem;
  background-color: var(--white);
  transition: all 300ms ease-in-out;
  transform-origin: 0.1875rem 0.0625rem;
}
.header__toggle > span:not(:last-child) {
  margin-bottom: 0.3125rem;
}
.header__nav {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}
.header__nav--open {
  position: absolute;
  display: flex;
  flex-direction: column;
  text-align: center;
  background-color: var(--darkGreen);
  width: 100%;
  left: 50%;
  transform: translate(-50%);
  height: auto;
  top: 6.125rem;
  padding: 20px;
  z-index: 990;
  margin: 0 auto;
  transition: all 300ms ease-in-out;
}
.header__media {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  margin-bottom: 0.625rem;
}
@media (max-width: 59.999375em) {
  .header__media {
    flex-direction: column;
  }
}
.header__search {
  margin-left: 1.875rem;
}
@media (max-width: 59.999375em) {
  .header__search {
    width: 100%;
    margin: 1.25rem 0 0;
  }
}
.header__search input[type=text] {
  font-size: 1rem;
  color: var(--lightGrey);
}
.header__search--placeholder {
  padding: 0.3125rem;
  border-radius: 0.3125rem;
  border: 0.0625rem solid var(--newGreen);
}
@media (max-width: 59.999375em) {
  .header__search--placeholder {
    width: 100%;
  }
}
@media (max-width: 59.999375em) {
  .header__social {
    width: 30%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}
.header__social a {
  margin-left: 1.25rem;
}
.header__social a:first-child {
  margin-left: 0;
}
.header__menu {
  display: flex;
  flex-direction: row;
  align-items: center;
  position: relative;
  padding-bottom: 0.625rem;
}
@media (max-width: 59.999375em) {
  .header__menu {
    flex-direction: column;
  }
}
.header__menu li {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-family: var(--font-header);
  font-size: 1.125rem;
  font-weight: 700;
  letter-spacing: 0.75px;
  list-style: none;
}
@media (max-width: 59.999375em) {
  .header__menu li {
    margin: 0.625rem 0;
  }
}

@media (max-width: 59.999375em) {
  .head {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 100%;
  }
}

.nav {
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  background-color: var(--darkGreen);
  width: 100%;
}
.nav__list {
  display: flex;
  padding: 0;
  margin: 0;
}
.nav__list--open {
  flex-direction: column;
  align-items: center;
  text-align: center;
  width: 100%;
  padding-left: 0;
  transition: all 300ms ease-in-out;
}
.nav__item:hover ul.submenu, .nav__item:focus ul.submenu {
  opacity: 1;
  visibility: visible;
  display: flex;
  flex-direction: column;
  width: 12rem;
}
@media (min-width: 43.75em) {
  .nav__item:hover ul.submenu, .nav__item:focus ul.submenu {
    width: 100%;
    opacity: 1;
    visibility: visible;
    display: flex;
    flex-direction: column;
    width: 12rem;
    width: 18rem;
  }
}
.nav__list, .nav__item {
  margin-left: 1.875rem;
}
.nav__list:first-child, .nav__item:first-child {
  margin-left: 0;
}
@media (max-width: 59.999375em) {
  .nav__list, .nav__item {
    display: none;
  }
}
.nav__list a, .nav__item a {
  color: var(--white);
  padding: 1rem 0;
  cursor: pointer;
}

.has-submenu {
  display: flex;
  flex-direction: column;
  position: relative;
}
.has-submenu ul.submenu {
  opacity: 0;
  visibility: hidden;
  transition: opacity 300ms ease-in-out;
}
@media (max-width: 59.999375em) {
  .has-submenu ul.submenu {
    opacity: 1;
    visibility: visible;
  }
}

.submenu {
  display: none;
  justify-content: center;
  padding-left: 0;
  width: 100%;
}
@media (min-width: 60em) {
  .submenu {
    display: none;
    justify-content: center;
    padding-left: 0;
    align-items: center;
    text-align: center;
    position: absolute;
    background-color: var(--darkGreen);
    padding: 0;
    top: 2em;
    margin-top: 1em;
  }
}
.submenu li {
  margin-left: 0;
}

@media (max-width: 59.999375em) {
  .submenu-active .submenu {
    display: block;
    background-color: var(--yellow);
  }
}

@media (max-width: 43.74875em) {
  li.subitem a {
    color: var(--darkGreen);
  }
}

.nav__item {
  text-align: center;
  display: none;
}

.active .nav__item {
  display: block;
}

.subitem {
  color: var(--white);
  width: 100%;
  margin: 0;
  padding: 0.75rem 0;
}
.subitem:hover, .subitem:focus {
  background-color: var(--yellow);
}
.subitem:last-child {
  padding-bottom: 0.75em;
}
.subitem a {
  padding: 0;
  gap: 0;
}
.subitem a:hover {
  color: var(--darkGreen);
}

@media (max-width: 59.999375em) {
  .hide-for-mobile {
    display: none;
  }
}

@media (min-width: 60em) {
  .hide-for-desktop {
    display: none;
  }
}

.drop {
  display: flex;
  flex-direction: column;
  z-index: 999;
}

.nav__item.has-submenu.submenu-active {
  background-color: transparent;
}

.intro {
  margin-top: 2.5rem;
  margin-bottom: 3.75rem;
  text-align: center;
}
.intro h1 {
  color: var(--darkGreen);
  margin-bottom: 2.5rem;
}
@media (min-width: ) {
  .intro h1 {
    font-size: 1.875rem;
    line-height: 2.25rem;
    margin-bottom: 1.875rem;
  }
}

.conf_tracks {
  display: flex;
  flex-direction: row;
  text-align: center;
  margin-bottom: 2.5rem;
  gap: 1rem;
}
@media (max-width: 43.74875em) {
  .conf_tracks {
    flex-direction: column;
    gap: 1.5rem;
  }
}

.conference,
.pharmacy,
.trade-show {
  transition: filter 0.25s ease-in-out;
  filter: grayscale(0%);
  margin-bottom: 0.625rem;
}

.conference:hover,
.pharmacy:hover,
.trade-show:hover {
  filter: grayscale(100%);
}

a.track_link {
  font-family: var(--font-header);
  font-size: 1.5rem;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 1px;
  color: var(--darkGreen);
}

a.track_link:hover {
  color: var(--northernBlue);
}

.concert-index {
  display: flex;
  flex-direction: row;
  gap: 1rem;
  margin-bottom: 3.75rem;
}
@media (max-width: 43.74875em) {
  .concert-index {
    flex-direction: column;
  }
}

.doyle-index {
  max-width: 30.625rem;
}

.index-info {
  background-color: var(--darkGreen);
  max-width: 43.75rem;
  width: 100%;
  padding: 1.25rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1.25rem;
}
@media (max-width: 43.74875em) {
  .index-info {
    padding: 1.25rem 0 2.5rem;
  }
}

.concert-name {
  padding: 0 0.625rem;
}

.concert-support {
  margin-bottom: 0.625rem;
}

.concert__banner {
  display: flex;
  flex-direction: row;
}

.sponsors__banner {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  background-color: var(--darkGreen);
  padding: 1.25rem 0.625rem;
