    width: 40%;
  }
}

.fullWidth__section {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
  height: auto;
  overflow: hidden;
  position: relative;
  justify-content: flex-start;
  align-items: center;
  margin-bottom: 3.75rem;
}
@media (max-width: 43.74875em) {
